import React from 'react'
import './Card.css'
import { Link } from 'react-router-dom'

export default function Card({card,setcard}) {
    let totalpprice=   card.reduce((price,tota)=>price + tota.price * tota.qty,0)

    function incrqty(product) {
        const exsit=card.find((x)=>x.id===product.id)
      
        setcard(card.map((item)=>{
          return item.id ===product.id?{...exsit,qty:exsit.qty+1}:item
        }))
        }
      
      function removeProduct(product){
      const exist =card.find((x)=>{
        return x.id=== product.id
      })
      if(exist.qty>0){
        setcard(card.filter((x)=>{
          return x.id!==product.id
        }))
      }
      }
        function desqty(product) {
          const exsit=card.find((x)=>x.id===product.id)
        
          setcard(card.map((item)=>{
            return item.id ===product.id?{...exsit,qty:exsit.qty-1}:item
          }))
          }
        
    return (

    <div className='Card'>
        <div className="emptyCard">
            {card.length===0&&
            <>
            <h1>empty Card</h1>
            <Link to="/product">Products</Link>
            </>}
        </div>
    <div className="cardContainer">
    {card.map((x,i)=>(
            <div className="productCardcho">
                <div className="imgProductCho">
                <img src={x.imageCover} alt="" />

                </div>
                <div className="textProductCho">
                <h1>{x.title.split(" ").slice(0,2).join(" ")}</h1>
                <h4>sub total :{x.qty>0?x.price * x.qty:0}</h4>

                <div className="priceCart">
    
    <div className="qty">
      <button className='incrqty' onClick={()=>incrqty(x)}>+</button>
      <input type="text" value={x.qty<0?0:x.qty} />
      <button className='desqty' onClick={()=>desqty(x)}>-</button>
    </div>
    <button className='xxx' onClick={()=>removeProduct(x)}><i class="fa-solid fa-x"></i></button>

      </div>
                </div>
            </div>
        ))}
    </div>
    <div className="checkOut">
    <h2 className='total'> {card.length===0?"":totalpprice+"$"}</h2>
{
    card.length>0?<button>checkOut</button>:""
}
    </div>

    </div>
  )
}
