import axios from 'axios'
import React, { useEffect, useState } from 'react'
import './Products.css'
import { Link } from 'react-router-dom'
import toast from 'react-hot-toast'
import Category from '../Category/Category'

export default function Products({card,setcard}) {
  const [query, setquery] = useState("")

  
const [loading, setloading] = useState(false)


  function handleChangeInput(e){
    setquery(e.target.value)
  }

  function addtoproduct(product){
    const exist=card.find((x)=>x.id===product.id)
    if(exist){
      toast("This product is added")
  }else{
    setcard([...card,{...product,qty:1}])
  }
}
  
    const [proudcts, setproudcts] = useState([])
   async function getProudcts(){
    setloading(true)
        let {data}= await axios.get("https://route-ecommerce.onrender.com/api/v1/products")
        setproudcts(data.data)
        setloading(false)
    }
    useEffect(()=>{
        getProudcts()
    },[])

  return (


    <>
    <Category/>
  
    <div className='Productss '>
        
   {loading?<div className='loading'><i class="fas fa-spin fa-spinner fa-7x  fa-spinner  loaderr"></i></div>:<>
    {proudcts.map((x,i)=>(
         
         <div className="product"key={i}>
               <Link id='toProductDet' to={`/productDetails/`+x._id} >
             <img src={x.images[0]} alt="" />
             <h1 className='h5'>{x.title.split(" ").slice(0,2).join(" ")}</h1>
             <div className="priceRating w-100 ">

<p className='price'>{x.price+" "}EGP </p>

<p className='rating'>{x.ratingsAverage}
<span className='star'><i class="fa-solid fa-star"></i></span>
</p>
</div>
</Link>

<div id='btncarrd' className="BTNaDDTOCARD">
<button onClick={()=>addtoproduct(x)}>Add to cart</button>
</div>
         </div>
   
     ))}
   </>}
    </div>
    </>
  )
}
