import React from 'react'
import { Link } from 'react-router-dom'
import './Notfound.css'
export default function Notfound() {
  return (
    <>
    <div className="notfound">

        <h1 className='  text-center'>If you did not found the pages please click on this Link</h1>
        <Link id='toProduct' to='/product'>
      <button>Show product</button>
</Link>
    </div>
    </>
  )
}
