import React from 'react'
import { Link } from 'react-router-dom'
import  './List.css'

export default function List({ menuOpen,setmenuOpen}) {
  return (
    <div className={'List '+(menuOpen&&"activelIST") }>
      <div className="listttmeennu">
      <ul>
            <Link onClick={()=>setmenuOpen(false)} id='linkLlist' to='/card'>
                <li>Cart</li>
                </Link>
            <Link onClick={()=>setmenuOpen(false)} id='linkLlist' to='/product'>
                <li>Product</li>
                </Link>

        </ul>
      </div>
      <div id='barList' onClick={()=>setmenuOpen(!menuOpen)} className={"barsssss "+(menuOpen&&"rotateBAR")}>
  <span></span><span></span><span></span>
</div>
    </div>
  )
}
